header {
    width: 105%;
    height: 140px;
    background-image: linear-gradient(to right, var(--primary), var(--compliment), var(--compliment), var(--compliment));
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    overflow: hidden;
    display: grid;
    grid-template-areas: "h1 nav  nav";
    grid-template-columns: 1fr 2fr 2fr;
    row-gap: 10px;
    justify-content: center;
    border-bottom: 10px solid var(--primaryaccent);
}

header nav {
    grid-area: nav;

    display: flex;
    flex-flow: row nowrap;
}

h1 {
    width: 10%;
    align-self: center;
    margin-left: 20px;
    font-size: 250%;
    font-style: italic;
    background: linear-gradient(90deg,  var(--compliment)50%, 
        var(--complimentaccent)50%, var(--complimentaccent)70%, var(--compliment)70%, 
        var(--compliment)75%, var(--complimentaccent)75%, var(--complimentaccent)85%, 
        var(--compliment)85%, var(--compliment)90%, var(--complimentaccent)90%, 
        var(--complimentaccent));
    color: rgb(232, 253, 139);
}

ul {
    list-style-type: none;
    margin: 0 auto;
    padding: 0;
    width: 90%;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-items: center;
    font-family: 'Bodoni Moda', serif;
}

header li{
    display: inline;
    font-style: italic;
    font-weight: bold;
    font-size: 20px;
    color: rgb(0, 37, 105);
    box-shadow: 0px 0px rgb(209, 209, 187);
    transition: box-shadow 1s;
}

nav button {
  -webkit-appearance: none;
  appearance: none;
  vertical-align: middle;
  color: inherit;
  font: inherit;
  border: 0;
  background: transparent;
  padding: 0;
  margin: 0;
  outline: 0;
  border-radius: 0;
  text-align: inherit;
}

li:hover {
    box-shadow: 20px 0px var(--compliment);
}
